<template>
  <div>
	<div class="row">
	  <div class="fade-in col-lg-6 col-md-12" v-for="person in filteredlist">
        <a href="#/profile" v-on:click="updateuser(person.id)">
		<card>
			<p class="card-text">
				<img style="float:left;margin-right:20px" height="150" width="150" :src="valimage(person)">
				<h4 class="card-title">{{person.name}}</h4>
				Class of {{person.year}}<br>
				{{person.degree}}<br>
				{{person.place}}<br>
				id: {{person.id}}
			</p>
		</card>
		</a>
      </div>
    </div>
	<div class="row">
	<div class="col-lg-12 col-md-6">
	<paginate
		v-model="$store.state.page"
		:page-count="totalpages"
		:page-range="3"
		:margin-pages="0"
		:click-handler="clickCallback"
		:prev-text="'<'"
		:next-text="'>'"
		:container-class="'pagination pagination-sm pagination-pills justify-content-center'"
		:page-class="'page-item'"
		:page-link-class="'page-link'"
		:prev-class="'page-item'"
		:prev-link-class="'page-link'"
		:next-class="'page-item'"
		:next-link-class="'page-link'"
		:hide-prev-next="true">
	</paginate>
	</div>
	</div>
  </div>
</template>
<style>
.pagination-pills .page-item .page-link {
  border-radius: 50rem !important;
  margin-left: 0.25rem;
  line-height: 1;
}

.pagination-pills .page-item:first-child .page-link {
  margin-left: 0;
}
</style>
<script>
	export default {
		data() {
			return {
				totalpages: 1
			}
		},
		methods: {
			updateuser (id) {
				this.$store.commit('curr_user', id)
			},
			valimage (person) {
				if (person.img){
					return person.img;
				}else{
					return 'img/def_user.jpg'
				}
			},
			notif () {
				this.$notify({type: 'danger', horizontalAlign: 'center', message: 'This is Portal is still under construction!'})
			},
			clickCallback (pageNum) {
				
			},
		},
		computed: {
			filteredlist() {
				var userlist = this.$store.state.users;
				var filtered = userlist.filter(m => (m.name.toLowerCase().indexOf(this.$store.state.search) > -1 || m.year.indexOf(this.$store.state.search) > -1));
				var num = filtered.length;
				this.totalpages = Math.ceil(num/12);
				var toShow = [];
				var init = this.$store.state.page - 1;
				for(var i = (init)*12; (i < (init + 1)*12) && (i < num); i++){
					toShow.push(filtered[i]);
				}
				return toShow
			},
		},
		created: function(){
			this.notif()
		}
	}
</script>